.Spinner_Loader__1twK-,
.Spinner_Loader__1twK-:before,
.Spinner_Loader__1twK-:after {
  border-radius: 50%;
}
.Spinner_Loader__1twK- {
  color: #521751;
  font-size: 11px;
  text-indent: -99999em;
  margin: 55px auto;
  position: relative;
  width: 10em;
  height: 10em;
  box-shadow: inset 0 0 0 1em;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}
.Spinner_Loader__1twK-:before,
.Spinner_Loader__1twK-:after {
  position: absolute;
  content: '';
}
.Spinner_Loader__1twK-:before {
  width: 5.2em;
  height: 10.2em;
  background: #fff;
  border-radius: 10.2em 0 0 10.2em;
  top: -0.1em;
  left: -0.1em;
  -webkit-transform-origin: 5.2em 5.1em;
  transform-origin: 5.2em 5.1em;
  -webkit-animation: Spinner_load2__2gkgc 2s infinite ease 1.5s;
  animation: Spinner_load2__2gkgc 2s infinite ease 1.5s;
}
.Spinner_Loader__1twK-:after {
  width: 5.2em;
  height: 10.2em;
  background: #fff;
  border-radius: 0 10.2em 10.2em 0;
  top: -0.1em;
  left: 5.1em;
  -webkit-transform-origin: 0px 5.1em;
  transform-origin: 0px 5.1em;
  -webkit-animation: Spinner_load2__2gkgc 2s infinite ease;
  animation: Spinner_load2__2gkgc 2s infinite ease;
}
@-webkit-keyframes Spinner_load2__2gkgc {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes Spinner_load2__2gkgc {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
